/*  @import './GoldenLayoutCustom.scss'; */
.lm_header {
  .lm_controls {
    /* //display:none; */
    /* // Turn off the confusing header controls. */
    .lm_popout, .lm_maximise, .lm_close {
      display:none;
    }

    height:100%;
    li {
      height:100%;
    }

    button, > li:not(.custom-toolbar) {
      border: 1px solid transparent;
      transition: 400ms all ease;
      margin-right: 0;

      &:hover {
        color: #333;
        border: 1px solid #adadad;
        background-color: #e6e6e6;
      }
    }

    button {
      padding: 0px 3px;
      background: transparent;
      svg {
        width: 10px !important;
        height: 10px !important;
      }
    }

    .custom-toolbar {
      margin-right: 5px;
      padding-top: 2px;

      /* // Remove when wanting to show the header */
      /* //display:none;*/

      /* //&:after, */
      .separator-right:after {
        content: '';
        position: absolute;
        height: 80%;
        top: 15%;
        right: 0;
        float: left;
        width: 1px;
        background: rgba(34, 36, 38, .7);
      }

      .separator-right {
        padding-right:4px;
      }

      i.icon {
        margin:0;
      }

      .ui.menu {
        .ui.item {
          padding:5px;
        }
      }
    }

    /* // The default buttons are too small */
    > li:not(.custom-toolbar) {
      zoom: 1.2;
      width: 14px;
    }
  }
}

/* end of GoldenLayoutCustom.scss */

:root {
  --unselected-tab-background: rgb(202,202,202);
  --active-tab-background: rgb(240,240,240); /* // Chrome */
  --selected-header-background: #452500;
}

.golden-layout-component {
  flex: 1;
  display: flex;
  max-width: 100%;
  max-height: 100%;
}

.lm_root {
  position: relative;
}

.lm_row > .lm_item {
  float: left;
}

.lm_content {
  overflow: hidden;
  background: #e1e1e1;

  > *:only-child {
    border:0;
    display:flex;
    flex-direction:column;

    > .mce-container {
      border: 0 !important;
    }
  }

  > div {
    max-width: 100%;
  }
}

.lm_dragging, .lm_dragging * {
  cursor: move !important;
  user-select: none;
}

.lm_maximised {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 40;
}

.lm_maximise_placeholder {
  display: none;
}

.lm_stack.lm_item {
  overflow: hidden;
  /* padding-top:3px; */
}

.lm_splitter {
  position: relative;

  z-index: 25;
}

.lm_splitter:hover {
  background: orange;
}

.lm_splitter.lm_vertical .lm_drag_handle {
  position: absolute;
  /* //height: 25px; */
  /* //top: -10px; */
  cursor: n-resize;
  width: 100%;
}

.lm_splitter.lm_vertical {
  height:3px !important;
  cursor:row-resize;
}

.lm_splitter.lm_horizontal {
  height: 100%;
  float: left;
  cursor:col-resize;
}

.lm_splitter.lm_horizontal .lm_drag_handle {
  position: absolute;
  width: 15px;
  cursor: e-resize;
  height: 100%;
}

.lm_drop_tab_placeholder {
  width: 100px;
  height: 10px;
  float: left;
  visibility: hidden;
}

/***********************************
* Drag Proxy
***********************************/
.lm_dragProxy {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
}

.lm_dragProxy .lm_header {
  background: transparent;
}

.lm_dragProxy .lm_content {
  border-top: none;
  overflow: hidden;
}

.lm_dropTargetIndicator {
  position: absolute;
  z-index: 2;
  display: none;
}

.lm_dropTargetIndicator .lm_inner {
  position: relative;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.lm_transition_indicator {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  width: 20px;
  height: 20px;
  z-index: 2;
}

.lm_popin {
  width: 20px;
  height: 20px;
  position: absolute;
  z-index: 9999;
  bottom: 0;
  right: 0;
}

.lm_popin > * {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.lm_popin > .lm_bg {
  z-index: 1;
}

.lm_popin > .lm_icon {
  z-index: 2;
}

:root {
  --unselected-tab-background: #e1e1e1;
  --active-tab-background: #fafafa;
  --selected-header-background: #452500;
}

/*
