$active-tab-background: #fafafa;
$selected-header-background: #452500;

@import '../../styles/themes/default-theme';

 */


.lm_dragProxy {
  .toolbar {
    display: none !important;
  }
}

.lm_goldenlayout {
  /* //transition: 200ms ease all;

  //background: url(data:image/png;base64,);
    */
}

.lm_dragProxy .lm_content {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.lm_dropTargetIndicator {
  box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.4);
  outline: 1px dashed #ccc;
  margin: 1px;
  transition: all 200ms ease;
}

.lm_dropTargetIndicator .lm_inner {
  background: #000;
  opacity: 0.1;
  filter: alpha(opacity=1);
}

.lm_splitter {
  background: #999;
  opacity: 0.8;
  transition: opacity 200ms ease;
}

.lm_splitter:hover {
  opacity: 1;
  background: #bbb;
}

.lm_selected .lm_header {
  background-color: var(--selected-header-background); /*$selected-header-background */;
}

.lm_maximised .lm_header {
  background-color: #fff;
}

.lm_header {
  --item-padding: 4px;
  position: relative;
  overflow: hidden !important;
  transition:height 400ms ease;

  * {
    box-sizing: content-box !important;
  }

  > ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  &.lm_selectable {
    cursor: pointer;
  }

  .lm_tabs {
    position: absolute;
    padding-left: 2px;
    margin-top: 2px;

    .lm_tab {
      white-space: nowrap;
      max-width: 100%;
      min-width:3em;
      border-radius: 5px 5px 0 0;
      transition: all 600ms ease;
      background: var(--unselected-tab-background);
      color: black;
      font-size: 14px;
      font-family: Arial, sans-serif;
      margin-right: 6px;
      border: 1px solid #ccc;
      border-bottom: none;
      display: flex;
      align-items: center;
      float: left;
      cursor: pointer;
      position: relative;
/*
      // &:first-of-type {
      //     border-top-left-radius: 0;
      // }

      // &:last-of-type {
      //     border-top-right-radius: 0;
      // }
*/
      i {
        &.lm_left {
          height: 19px;
          position: absolute;
          left: -2px;
          width: 2px;
          top: 0;
        }

        &.lm_right {
          height: 19px;
          position: absolute;
          width: 2px;
          right: -2px;
          top: 0;
        }
      }

      .lm_title {
        text-overflow: ellipsis;
        display: inline-block;
        flex: 1 1 auto;
        overflow: hidden;
        user-select: none;
        padding:0 5px;
      }

      .lm_close_tab {
        &:before {
          content: 'x';
          transition: 400ms ease color;
        }

        &:hover:before {
          color: red;
        }

        text-align: center;
        padding-right: var(--item-padding);
        align-self: center;
        opacity: .7;
        margin: -2px 0 0 5px;
/*
        //background-position: center center;
        //background-repeat: no-repeat;
        //background-image: url(data:image/png;base64,);
        //top: auto !important;
*/
      }

      &.lm_active,
      &:only-child,
      &:hover {
        background: var(--active-tab-background); /* $active-tab-background; */
      }

      &.lm_active {
        border-bottom: none;
        box-shadow: 2px -2px 2px -2px rgba(0, 0, 0, 0.2);
      }

      .lm_close_tab:hover,
      &.lm_active .lm_close_tab {
        opacity: 1;
        filter: alpha(opacity=100);
      }
    }
  }

  .lm_controls {
    position: absolute;
    right: 0px;
    bottom: 0px;

    .lm_close {
      background-image: url(data:image/png;base64,);
    }

    > li {
      background-position: center center;
      background-repeat: no-repeat;
      position: relative;
      width: 18px;
      height: 18px;
      float: left;
      text-align: center;
      cursor: pointer;
      transition: opacity 300ms ease;
      opacity: .9;

      &:hover {
        opacity: 1;
        filter: alpha(opacity=100);
      }

      &.lm_popout {
        background-image: url(data:image/png;base64,);
      }

      &.lm_maximise {
        background-image: url(data:image/png;base64,);
      }
    }
  }
}

.lm_maximised .lm_controls .lm_maximise {
  background-image: url(data:image/png;base64,);
}

.lm_transition_indicator {
  background-color: #000;
  border: 1px dashed #555;
}

.lm_popin {
  cursor: pointer;
}

.lm_popin .lm_bg {
  background: #000;
  opacity: 0.7;
  filter: alpha(opacity=70);
}

.lm_popin .lm_icon {
  background-position: center center;
  background-repeat: no-repeat;
  opacity: 0.7;
  filter: alpha(opacity=70);
  background-image: url(data:image/png;base64,);
}

.lm_popin:hover .lm_icon {
  opacity: 1;
  filter: alpha(opacity=100);
}

.lm_tab.is-editable {
  input.title {
    display: none;
  }

  &.is-editing {
    input.title {
      border: 0;
      top: -1px;
      display: inline-block;
    }

    .lm_title {
      display: none;
    }
  }
}
