.root {
    flex: 1;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    border-radius: 3px;
    background-color: white;
    position: relative;

    .fileDropper {
        display: none;
        position: absolute;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.1);
        z-index: 11;
    }

    :global(.bp3-navbar) {
        border-bottom: none !important;
        padding-bottom: 0;

        :global(.bp3-navbar-group) {
            position: relative;
            overflow: hidden;

            .file-selection-button {
                cursor: pointer;
                position: absolute;
                top:0;
                left: 0;
                opacity: 0;
            }
        }
    }

    &.dragging-file {
        box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 4px 8px rgba(16, 22, 26, 0.2), 0 18px 46px 6px rgba(16, 22, 26, 0.2);

        .fileDropper {
            display: block;
        }
    }

    &:global(.InputSpecificationComponent__indent) {
        :global(.bp3-navbar), .table {
            padding-left: 0;
        }
    }

    .table {
        white-space: nowrap;

        :global(thead tr th) {
            &:nth-child(1) {width: 1%}
            &:nth-child(3) {width: 1%}
            &:nth-child(4) {width: 1%}
        }

        th, td {
            padding: 10px !important;
            &:not(:nth-child(3)) { line-height: 30px; }
        }

        .uploaded-list td:nth-child(1) { padding: 17px 0px 17px 10px !important;}
        .uploading-list {
            border-top: none;

            .information-cells td {
                border-bottom: none;
            }

            :global(td:nth-child(1)):not(.uploading-progress-cell) {
                padding: 15px 0px 15px 10px !important;
            }

            .uploading-progress-cell {
                padding: 0px !important;

                :global(.progress) {
                    border-radius: 0;
                }
            }
        }
    }
}