.root {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    height: 100%;

    .header {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 11;
        width: 100%;

        display: flex;
        flex-direction: row;
        background-color: white;
        border-bottom: 1px solid var(--theme-color-secondary);

        .header-left { flex: 1; align-content: center; text-align: left; padding:3px; }
        .header-right { flex: 0; align-content: center; }
    }

    .body {
        flex: 1;
        overflow: hidden;

        :global(.splitter .pane) {
            overflow: hidden;
        }

        .auto-size-box {
            padding: 0;
            margin: 0;
            width: fit-content;
            min-width: 100%;
        }

        .container-reset {
            padding: 0;
            margin: 0;
            overflow: hidden;
        }

        .main-box {
            padding-top: 37px;
            overflow-x: auto;
            overflow-y: hidden;
            display: flex;
            flex-direction: column;

            &.enableScroll{
                overflow-y: auto;
                width: 100%;
                height: 100%;

                .cellPadding {
                    margin: 10px;
                    padding:0;
                }
            }

            .main-group { padding: 10px; }

            .main-cell {}
        }

        .side-box {
            padding-top: 37px;
            overflow-x: auto;
            overflow-y: auto;
            display: flex;
            flex-direction: column;

            .side-group { padding:  10px; }

            .side-cell {}
        }

        .sizeSensor {
            width: 100%;
            height: 0;
        }
    }

    .footer {
        flex: 0;
        text-align: right;
        border-top: 1px solid var(--theme-color-secondary);
    }
}