.root {
    display: flex;
    flex:1;
    justify-content: center;
    padding-top: 55px;
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    background-position: center;

    iframe {
        height: 431px;
        width: 415px;
        border: none;
        margin-left: -1px;
        margin-top: -1px;
    }

    .header {
        display: flex;
        background: #f1f4f6;
        border-radius: 6px 6px 0 0;
        min-height: 65px;
        background-image: linear-gradient(#f1f4f6,#e9edf0);
        border-bottom: 1px solid rgba(40,69,85,.11);
        padding: 21px 20px;
        margin: 0;
        font-size: 18px;
        color: #41444a;
        font-weight: 700;
        border-bottom: 1px solid #DDE3E6;
        text-align: left;

        img {
            height: 32px;
        }

        span {
            margin: auto;
        }
    }


    .panel {
        box-shadow: 0 0 0 1px rgba(14,41,57,.12), 0 2px 5px rgba(14,41,57,.44), inset 0 -1px 2px rgba(14,41,57,.15);
        background: #fff;
        width: 412px;
        height: 505px;
        overflow: hidden;
    }
}