.dialog-portal :global(.wj-dropdown-panel) {
    z-index: 65536 !important; /* z-index should bigger then Dialog */
}

.dialog {
    width: 100%;
    max-width: calc(100vw - 60px);
    max-height: calc(100vh - 60px);
    padding-bottom: 0;
    overflow: hidden;

    .dialog-body {
        position: relative;
        min-height: 150px;
        overflow: auto;
    }
}

:global(.RecalibrationToolPage__main-box .RecalibrationToolPage__auto-size-box) {
    width: 100% !important;
}